import React from 'react';
import fetch from 'isomorphic-fetch';

const Control = ({ data }) => (
  <p>
            control:
    {' '}
    {console.log('a', data)}
  </p>
);

Control.getInitialProps = async ({ query }) => {
  const res = await fetch('http://localhost:3000/user/1');
  const json = await res.json();
  return { data: json };
};

export default Control;
